body {
    background-color: #f9f1e4;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    margin: 0;
}

.box {
    border: 2px black solid;
    background-color: #f1bd93d8;
    width: 400px;
    max-height: 400px;
    overflow-y: auto; 
    overflow-x: hidden;
    text-align: center;
    padding: 20px; 
    border-radius: 25px;
}

.box h1 {
    text-align: center;
}

.input {
    border-radius: 10px;
    border: 2px solid black;
    height: 43px;
    width: 170px;
    margin-left: 10px;
}

.add {
    border: 2px solid black;
    margin-left: 5px;
    height: 45px;
    width: 90px;
    border-radius: 10px;
}

#taskList {
    list-style-type: none; 
    padding: 0;
    margin-top: 40px; 
    margin-left: -10px;
    width: 95%; 
    
}

.task {
    background-color: #816c5ad8;
    color: white;
    padding: 12px;
    margin-top: 15px;
    border: 2px solid black;
    border-radius: 10px; 
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1); 
    display: flex;
    align-items: center; 
    font-size: 18px;
    min-width: 300px; 
    max-width: 400px; 
    width: 100%; 
    box-sizing: border-box; 
    cursor: pointer;
    position: relative; 
    padding-right: 240px;
}

.task .task-text {
    flex-grow: 1; 
    overflow-wrap: break-word; 
}

button.Delete,
button.Edit {
    background-color: transparent;
    border: none;
    border-radius: 50%; 
    color: red; 
    font-size: 14px;
    width: 24px;
    height: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    position: absolute; 
    bottom: 10px;
}

button.Edit {
    color: blue;
    right: 90px; 
}

button.Delete {
    color: red;
    right: 20px; 
}

.task p {
    margin: 0; 
}
.task input[type="checkbox"] {
    margin-right: 5px; 
    vertical-align: middle; 
}

.task-content.underline::after {
    background-color: red;
}